@import '../../common.scss';

main {
  display: flex;
  justify-content: space-between;
  padding: 20px;

  .box {
    margin: 0;
  }

  .left-section {
    width: 100%;
    margin-right: 20px;
  }

  .resources {
    width: 30%;
    max-width: 200px;
  }
  
  #topbar {
    display: flex;
    justify-content: space-between;
    height: 100px;

    .menu {
      width: 100%;
    }

    .box {
      margin-left: 40px;
    }
  }
  
  .game-panel {
    margin: 20px 0 0 0;
    padding: 30px;
    width: 100%;  
    height: calc(100vh - 50px - 100px - 3 * 20px);
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
  }

  aside {
    width: 28%;
  }
}