@import '../../common.scss';

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input {
  display: block;
  margin-bottom: 20px;
  width: 100%;

  input {
    background-color: inherit;
    border: none;
    border-bottom: 2px solid $color-highlighted;
    color: $color-dark-text;
    font-size: inherit;
    padding: 5px;
    width: 100%;
  }

  input:focus {
    border-bottom: 2px solid $color-highlighted-dark;
    outline: none;
  }

  input::placeholder {
    color: $color-dark-text-faded;
  }

  .error {
    color: $color-danger;
    padding-left: 5px;
    margin-top: 5px;
    height: $font-size-normal;
  }

  &.invalid input {
    border-bottom: 2px solid $color-danger;
  }

}
