@import './common.scss';

* {
  box-sizing: border-box;
}

html {
  padding: 0;
}

body {
  margin: 0;
  background-image: url('/images/background.png');
  font-family: 'Roboto', sans-serif;
  font-size: $font-size-normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.icon {
  height: 20px;
  width: 20px;
}