@import '../../common.scss';

.menu {
  height: 100%;
  display: flex;
  flex-direction: row;

  .menu-item {
    background-color: $color-panel-background;
    cursor: pointer;
    height: 100%;
    width: 25%;
    text-align: center;

    img {
      height: 60%;
    }

    a {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
      text-decoration: none;
      color: $color-dark-text;
      padding: 10px 30px;
    }

    p {
      margin: 0;
      margin-top: 10px;
      padding: 0;
    }

    &:hover {
      background-color: $color-panel-background-dark;

      a {
        color: $color-light-text;
      }
    }

    &:first-of-type {
      border-top-left-radius: $border-radius;
      border-bottom-left-radius: $border-radius;
    }

    &:last-of-type {
      border-top-right-radius: $border-radius;
      border-bottom-right-radius: $border-radius;
    }

    &.active {
      background-color: $color-panel-background-darker;
       a {
        color: $color-light-text;
       }
    }
  }
}