@import '../../common.scss';

.btn {
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  @include box-shadow;
}

.cta {
  background-color: $color-highlighted;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
}

.cta:hover {
  background-color: $color-highlighted-dark;
}