@import '../../common.scss';

.construction-log {
  img {
    height: 50px;
    width: 50px;
    margin-right: 10px;
  }

  .box {
    padding: 10px;
  }

  .construction-log-title {
    display: flex;
    flex-direction: row;
    justify-content: left;

    span {
      font-size: $font-size-medium;
      font-weight: bold;
    }
  }

  .construction-item {
    margin: 20px 0;
    display: flex;
    flex-direction: row;
    justify-content: left;

    .construction-item-body {
      width: 100%;
    }
  
    .construction-item-info {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 5px;
    }

    progress {
      width: 100%;
    }
  }

  
}