$color-dark-text: #333;
$color-dark-text-faded: #3336;
$color-light-text: #fff;
$color-highlighted: #009587;
$color-highlighted-dark: #005E55;
$color-danger: #DB4437;
$color-success: lightGreen;
$color-fail: orange;
$color-panel-background: #fff8;
$color-panel-background-dark: #aaa8;
$color-panel-background-darker: #aaa4;

$font-size-big: 2rem;
$font-size-medium: 1.3rem;
$font-size-normal: 1rem;

$border-radius: 2px;

@mixin box-shadow {
  box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.5);
  -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.5);
  -moz-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.5);
}

.hidden {
  visibility: hidden;
}