@import '../../common.scss';

#registration,
#login {
  background-size: cover;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 600px;

  .title {
    color: $color-light-text;
  }

  .box {
    width: 100%;

    a {
      margin-top: 20px;
      text-decoration: none;
      color: $color-dark-text;
    }

    a:hover {
      color: $color-highlighted-dark;
    }
  }

}