@import '../../common.scss';

.building-item {
  width: 130px;
  padding: 15px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  cursor: pointer;

  .building-title {
    text-align: center;
    margin-top: 5px;

    p {
      margin: 5px;
      text-transform: capitalize;
    }
  }

  &.in-progress {
    cursor:not-allowed;

    img {
      opacity: 0.5;
    }
  }

  &.in-progress:hover {
    background-color: inherit;
  }

  &:hover {
    background-color: $color-panel-background-dark;
  }

}

.empty-slot {
  padding: 0;
  margin: 15px;
  height: 100px;
  width: 100px;
  background-color: $color-dark-text-faded;
  border-radius: 10px;
}

.MuiBox-root {

  .cta {
    display: flex;
    margin: 15px 0;

    img {
      width: 40px;
      height: 40px;
      margin-right: 15px;
    }

    .btn-text {
      width: 100%;
    }

    .btn-title {
      margin-bottom: 5px;
    }

    .btn-details {
      font-weight: normal;
      width: 100%;
      display: flex;
      justify-content: space-between;

      span {
        display: block;
      }
    }
  }

}