@import '../../common.scss';

.buildings {
  display: flex;
  flex-wrap: wrap;
  flex-basis: 100px;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
}

#building-page {
  display: flex;
  justify-content: space-between;

  .details-left {
    width: 15%;

    .building-title {
      margin: 10px 0;
      text-align: center;

      p {
        margin: 0;
      }
    }
  }

  .details-right {
    width: 80%;

    p {
      margin: 0 0 10px 0;
    }

    .buttons {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 30px;

      .cta {
        width: 300px;
        text-transform: none;
        font-weight: normal;
        margin: 10px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
          width: 40px;
          height: 40px;
        }

        div {
          width: 100%;
          padding: 0 20px;
        }
        
        p {
          margin: 5px 0;
        }
  
        .btn-title {
          font-weight: bold;
        }
      }
    }

  }
}