@import '../../common.scss';

.resources {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 5px 10px;

  .resource-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 45%;

    .amount {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      font-size: $font-size-medium;

      .icon {
        margin: 0 5px;
      }
    }

    .generation {
      
      &.positive {
        color: $color-success;
      }
      
      &.negative {
        color: $color-fail;
      }
    }
  }
}